.Footer {
  display: flex;
  justify-content: space-between;
  padding: 18px 20px 14px;
  color: $secondaryTextColor;
  font-size: 13px;
  line-height: 18px;
  border-top: 1px solid $lightSeparatorColor;

  &-version {
    display: inline-block;
  }

  &-copyright {
    display: inline-block;
    margin-left: 32px;
  }
}

@include s-screen {
  .Footer {
    display: none;
  }
}
